html {
  height: 100%;
}
body {
  background: -webkit-linear-gradient(60deg, #3e8da5, #433a93);
  background: linear-gradient(30deg, #3e8da5, #433a93);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  font-size: 45px;
  color: white;
  text-align: center;
  margin-bottom: 50px;
  width: 100%;
}

a:link {
  color: inherit; }

a:visited {
  color: inherit; }

.about {
  border-radius: 9px;
  margin: 5% auto 10% auto;
  padding-top: 20px;
  font-size: 30px;
  vertical-align: middle;
  background: #e9e9e9;
  font-family: "Monaco", "Courier New";
  text-align: left;
  font-size: 13px;
  color: #fafafa;
  min-width: 30vw; }

.aboutWrapper {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }

@media (max-width: 500px) {
  .about {
    display: block;
    margin-top: auto;
    margin-bottom: auto;
    width: 90%; }
  .contents {
    font-size: 1em; } }

.contents {
  padding: 20px; }

#console {
  margin: 0 auto;
  background: #2d2d3e;
  width: 100%;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
  border-top-left-radius: 0%;
  border-top-right-radius: 0%; }

span.term {
  color: white;
  padding-left: 20px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.project {
  -webkit-align-content: center;
          align-content: center;
  color: white;
  font-size: 18px;
  padding-bottom: 100px;
  margin: 0 10% 40% 10%;
  position: static; }
  .project .content {
    padding-bottom: 3%;
    width: 100%; }
  .project .proj_details {
    margin: 0 4% 0 4%;
    font-family: "Source Sans Pro"; }
    .project .proj_details .language_container {
      margin-top: 4%;
      width: 100%;
      margin-bottom: 4%; }
    .project .proj_details .proj_languages {
      border-width: 1px;
      border-style: solid;
      border-color: #005eff;
      text-transform: uppercase;
      font-weight: 100;
      display: inline-block;
      color: #005eff;
      font-size: 0.7em;
      padding: 5px 5px 5px 5px;
      margin-right: 5px;
      border-radius: 3px;
      border-spacing: 20%; }
    .project .proj_details .proj_title {
      font-size: 20px;
      font-size: 1em;
      width: 100%;
      margin-bottom: 3%; }
    .project .proj_details .description {
      font-size: 0.8em;
      width: 100%; }
  .project .node {
    float: left;
    background-color: #131313;
    width: 25%;
    border-radius: 1%;
    margin-left: 1%;
    margin-right: 1%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content; }

@media (max-width: 800px) {
  .project .node {
    width: 48%; } }

.App {
